import pick from 'lodash/pick'
import PropTypes from 'prop-types'

import { getKeyStyles } from '../key-units'
import styles from './styles.module.css'

const getPosition = key => pick(key, ['x', 'y'])
const getRotation = key => {
  const { rx, ry, r } = key
  return { x: rx, y: ry, a: r }
}
const getSize = key => {
  const { w = 1, u = w, h = 1 } = key
  return { u, h }
}

function Placer (props) {
  const { onClick, key_: key, children } = props
  const position = getPosition(key)
  const rotation = getRotation(key)
  const size = getSize(key)
  const positioningStyle = getKeyStyles(position, size, rotation)

  return (
    <div
      className={styles.placer}
      style={positioningStyle}
      data-u={size.u}
      data-h={size.h}
      onClick={onClick}
    >
      {children}
    </div>
  )
}

Placer.propTypes = {
  onClick: PropTypes.func,
  key_: PropTypes.shape({
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired,
    a: PropTypes.number,
    rx: PropTypes.number,
    ry: PropTypes.number,
    u: PropTypes.number,
    h: PropTypes.number
  })
}

export default Placer
