import times from 'lodash/times'

const DEFAULT_BIND_BEHAVIOUR = '&trans'
const createEmptyBinding = (value = DEFAULT_BIND_BEHAVIOUR) => ({
  value, params: []
})

export function handleCreateLayer (state, action) {
  const { layout, keymap } = state
  const index = keymap.layers.length

  const bindings = times(layout.length, createEmptyBinding)
  const newNames = [ ...keymap.layer_names, `Layer #${index}` ]
  const newLayers = [ ...keymap.layers, { bindings } ]

  return {
    ...state,
    keymap: {
      ...state.keymap,
      layer_names: newNames,
      layers: newLayers
    }
  }
}

export function handleUpdateLayerBindings (state, action) {
  const { index, updated } = action
  const { layers } = state.keymap

  return {
    ...state,
    keymap: {
      ...state.keymap,
      layers: [
        ...layers.slice(0, index),
        {...layers[index], bindings: updated},
        ...layers.slice(index + 1)
      ]
    }
  }
}

export function handleRenameLayer (state, action) {
  const { layerName } = action
  const { activeLayer, layer_names : names } = state

  const newNames = [
    ...names.slice(0, activeLayer),
    layerName,
    ...names.slice(activeLayer + 1)
  ]

  return {
    ...state,
    keymap: {
      ...state.keymap,
      layer_names: newNames
    }
  }
}

export function handleDeleteLayer (state, action) {
  const { index } = action
  const { activeLayer, keymap } = state
  const newNames = [...keymap.layer_names]
  const newLayers = [...keymap.layers]

  newNames.splice(index, 1)
  newLayers.splice(index, 1)

  const newActiveLayer = activeLayer > newLayers.length - 1
    ? Math.max(0, newLayers.length - 1)
    : activeLayer

  return {
    ...state,
    activeLayer: newActiveLayer,
    keymap: {
      ...state.keymap,
      layer_names: newNames,
      layers: newLayers
    }
  }
}
