import PropTypes from 'prop-types'
import { useCallback, useMemo, useState } from 'react'

import Context from './Context'
import KeyboardLayout from './KeyboardLayout'
import LayerSelector from './LayerSelector'
import * as reducers from './reducers'
import { getWrapperStyle } from './util'


function Keyboard(props) {
  const { layout, keymap, onUpdate } = props
  const [activeLayer, setActiveLayer] = useState(0)

  const wrapperStyle = useMemo(() => getWrapperStyle(layout), [layout])

  const handleCreateLayer = useCallback(function () {
    onUpdate(reducers.handleCreateLayer({ layout, keymap }).keymap)
  }, [keymap, layout, onUpdate])

  const handleUpdateLayer = useCallback(function(index, updated) {
    onUpdate(reducers.handleUpdateLayerBindings({ keymap }, { index, updated }).keymap)
  }, [keymap, onUpdate])

  const handleRenameLayer = useCallback(function (layerName) {
    onUpdate(reducers.handleRenameLayer({ activeLayer, keymap }, { layerName }).keymap)
  }, [keymap, activeLayer, onUpdate])

  const handleDeleteLayer = useCallback(function (index) {
    const newState = reducers.handleDeleteLayer({ activeLayer, keymap }, { index })
    setActiveLayer(newState.activeLayer)
    onUpdate(newState.keymap)
  }, [keymap, activeLayer, setActiveLayer, onUpdate])

  return (
    <Context keymap={keymap}>
      <LayerSelector
        layers={keymap.layer_names}
        activeLayer={activeLayer}
        onSelect={setActiveLayer}
        onNewLayer={handleCreateLayer}
        onRenameLayer={handleRenameLayer}
        onDeleteLayer={handleDeleteLayer}
      />
      <div style={wrapperStyle}>
        <KeyboardLayout
          mode="edit"
          data-layer={activeLayer}
          layout={layout}
          bindings={keymap.layers[activeLayer].bindings}
          onUpdate={event => handleUpdateLayer(activeLayer, event)}
          onSelect={() => {}}
        />
      </div>
    </Context>
  )
}

Keyboard.propTypes = {
  layout: PropTypes.array.isRequired,
  keymap: PropTypes.object.isRequired,
  onUpdate: PropTypes.func.isRequired
}

export default Keyboard
