import PropTypes from 'prop-types'
import { useMemo } from 'react'

import Placer from './KeyPlacer'
import Key from './Keys/Key'

function KeyboardLayout(props) {
  const { mode, layout, bindings, onUpdate, onSelect } = props
  const normalized = layout.map((_, i) => (
    bindings[i] || {
      value: '&none',
      params: []
    }
  ))

  const handleUpdateBind = useMemo(() => function(keyIndex, updateBinding) {
    onUpdate([
      ...normalized.slice(0, keyIndex),
      updateBinding,
      ...normalized.slice(keyIndex + 1)
    ])
  }, [normalized, onUpdate])

  function handleSelectKey (event, index) {
    if (onSelect) {
      event.stopPropagation()
      onSelect(index)
    }
  }

  return (
    <div style={{ position: 'relative' }}>
      {layout.map((key, i) => (
        <Placer
          key={i}
          key_={key}
          onClick={event => handleSelectKey(event, i)}
        >
          <Key
            editable={mode === 'edit'}
            label={key.label}
            value={normalized[i].value}
            params={normalized[i].params}
            onUpdate={bind => handleUpdateBind(i, bind)}
          />
        </Placer>
      ))}
    </div>
  )
}

KeyboardLayout.propTypes = {
  mode: PropTypes.oneOf(['selection', 'edit']).isRequired,
  layout: PropTypes.array.isRequired,
  bindings: PropTypes.array.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default KeyboardLayout
